resizeTo(1, 1)
onload = () => {
  const queryString = window.location.search
  const urlParams = new URLSearchParams(queryString)
  const audio = new Audio(urlParams.get('src')!)
  audio.volume = Number(urlParams.get('volume') ?? 1)
  audio.loop = true
  audio.play()
  setTimeout(close, Number(urlParams.get('length') ?? 1000))
}
